import {
    START_GAME,
    SCREEN_MAIN,
    SCREEN_GAME,
    SCREEN_RESULT,
    YES,
    NO,
    RANDOM_IMAGE,
    GAME_OVER,
    MAIN_MENU
} from './constants'

let initialState = {
    screen: SCREEN_MAIN,
    currentImageId: null,
    prevImageId: null,
    correctAnswers: 0,
    images: [
        require('./images/dogs/1.png'),
        require('./images/dogs/2.png'),
        require('./images/dogs/3.png'),
        require('./images/dogs/4.png'),
        require('./images/dogs/5.png'),
        require('./images/dogs/6.png'),
        require('./images/dogs/7.png'),
        require('./images/dogs/8.png'),
        require('./images/dogs/9.png'),
        require('./images/dogs/10.png'),
        require('./images/dogs/11.png'),
        require('./images/dogs/12.png'),
        require('./images/dogs/13.png')
    ]
};

export default function reducer (state = initialState, action) {
    switch(action.type) {
        case NO: {
            let {correctAnswers, currentImageId, prevImageId} = state
            let isCorrect = currentImageId !== prevImageId
            if (isCorrect)
                correctAnswers++
            return {
                ...state,
                correctAnswers,
                isAnswered: true,
                isCorrect
            }
        }

        case YES: {
            let {correctAnswers, currentImageId, prevImageId} = state
            let isCorrect = currentImageId === prevImageId
            if (isCorrect)
                correctAnswers++
            return {
                ...state,
                correctAnswers,
                isAnswered: true,
                isCorrect
            }
        }

        case START_GAME: {
            return {
                ...state,
                screen: SCREEN_GAME
            }
        }

        case RANDOM_IMAGE: {
            let {currentImageId, images} = state
            let min = 0
            let max = images.length - 1
            let isUsePrevImage = Math.round(Math.random() * 3) === 2
            let newImageId = Math.round(Math.random() * (max - min) + min)

            return {
                ...state,
                currentImageId: isUsePrevImage && currentImageId !== null ? currentImageId : newImageId,
                prevImageId: currentImageId
            }
        }

        case GAME_OVER: {
            return {
                ...state,
                screen: SCREEN_RESULT
            }
        }

        case MAIN_MENU: {
            return {
                ...state,
                screen: SCREEN_MAIN,
                correctAnswers: 0,
            }
        }

        default:
            return state;
    }
}


