
import React from 'react'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as actions from '../actions'
import classSet from 'react-classset'

const AnswerIndicator = ({correct}) => {
    let classes = classSet({
        shape: true,
        correct,
        incorrect: !correct
    })
    return (
        <div className="answer-indicator">
            {correct ? <div key="correct" className="correct"></div> : <div key="incorrect" className="incorrect"></div>}
        </div>
    )
}
const theComponent = ({no, yes, correctAnswers, imageSrc, isCorrect, isAnswered}) => {
    let time = Date.now()
    return (
        <div>
            <div className="image-container">
                <img className="image" alt="" src={imageSrc} />
                { isAnswered && <AnswerIndicator key={time} correct={isCorrect} />}
            </div>
            <button onClick={no}>No</button> <button onClick={yes}>Yes</button>
        </div>
    )
}

const mapStateToProps = (state) => {
    let {correctAnswers, currentImageId, images, isAnswered, isCorrect} = state

    return {
        correctAnswers: correctAnswers,
        imageSrc: images[currentImageId],
        isAnswered,
        isCorrect
    }
}

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators(actions, dispatch),
})

export default connect(mapStateToProps, mapDispatchToProps)(theComponent)


