import {
    INITIALIZE,
    START_GAME,
    YES,
    NO,
    RANDOM_IMAGE,
    GAME_OVER,
    MAIN_MENU
} from './constants'
let timerId = null;
export function initialize() {
    return {
        type: INITIALIZE
    }
}

export function startGame() {
    return (dispatch, getState) => {
        setTimeout(() => dispatch(gameOver()), 1000 * 30);
        dispatch({type: START_GAME})
        dispatch(randomImage())
    }
}

export function yes() {
    return (dispatch, getState) => {
        dispatch({type: YES})
        dispatch(randomImage())
    }
}

export function no() {
    return (dispatch, getState) => {
        dispatch({type: NO})
        dispatch(randomImage())
    }
}

export function randomImage() {
    return {
        type: RANDOM_IMAGE
    }
}

export function gameOver() {
    return {
        type: GAME_OVER
    }
}

export function mainMenu() {
    return (dispatch, getState) => {
        dispatch(randomImage())
        dispatch({ type: MAIN_MENU })
    }
}


