import React from 'react'
import * as actions from '../actions'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'

const theComponent = ({startGame, imageSrc}) => {
    return (
        <div>
            <div className="image-container">
                <img className="image" alt="" src={imageSrc} />
            </div>
            <div>
                <button onClick={startGame}>Start game</button>
            </div>
        </div>
    )
}

const mapStateToProps = (state, ownProps) => {
    let {currentImageId, images} = state

    return {
        imageSrc: images[currentImageId]
    }
}

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators(actions, dispatch),
})

export default connect(mapStateToProps, mapDispatchToProps)(theComponent)


