import {
  SCREEN_MAIN,
  SCREEN_GAME,
  SCREEN_RESULT
} from '../../constants'
import React, { Component } from 'react';
import './App.css';
import Main from '../Main'
import Game from '../Game'
import Result from '../Result'
import {connect} from 'react-redux'

class App extends Component {
  render() {
    return (
      <div className="App">
          <h3>Remember image</h3>
        {this.props.screen === SCREEN_MAIN ? <Main /> : null }
        {this.props.screen === SCREEN_GAME ? <Game /> : null }
        {this.props.screen === SCREEN_RESULT ? <Result /> : null }
      </div>
    );
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    screen: state.screen
  }
} 
export default connect(mapStateToProps)(App)



